<template>
  <el-row class="container">
    <el-col :span="24" class="header">
      <el-col :span="10" class="logo" :class="['width-transition', isCollapse ? 'logo-collapse-width' : 'logo-width']">
        {{ isCollapse ? '' : sysName }}
      </el-col>
      <el-col :span="10">
        <div class="tools" @click.prevent="collapse">
          <i class="fa fa-align-justify"></i>
        </div>
      </el-col>
      <el-col :span="10" class="userinfo">
        <el-badge :value="12" class="item">
          <el-popover
            placement="bottom"
            width="400"
            trigger="click">
            <div style="margin-top: 10px">第1条消息 点击打开弹出层 或者 跳转页面</div>
            <div style="margin-top: 10px">第2条消息</div>
            <div style="margin-top: 10px">第3条消息</div>
            <div style="margin-top: 10px">第4条消息</div>
            <div style="margin-top: 10px">第5条消息</div>
            <div style="margin-top: 10px">第6条消息</div>
            <div style="margin-top: 10px">第7条消息</div>
            <div style="margin-top: 10px">第8条消息</div>
            <span @click="openMessageLayer" slot="reference">消息</span>
          </el-popover>
        </el-badge>
        <el-dropdown trigger="click">
          <span class="el-dropdown-link userinfo-inner">
            <el-avatar icon="el-icon-user-solid"></el-avatar>
            {{sysUserName}}
          </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item>我的消息</el-dropdown-item>
            <el-dropdown-item>设置</el-dropdown-item>
            <el-dropdown-item divided @click.native="logout">退出登录</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </el-col>
    </el-col>
    <el-col :span="24" class="main">
      <aside :class="['width-transition', isCollapse ? 'menu-collapsed' : 'menu-expanded']">
        <!--导航菜单 unique-opened -->
        <el-menu :default-active="$route.path" class="el-menu-vertical width-transition"
          @open="handleOpen" @close="handleClose" :collapse="isCollapse"
          @select="handleSelect" router>
          <template v-for="(item,index) in $router.options.routes" >
            <el-submenu 
              :index="index + '' "
              v-if="!item.hidden"
              :key="index" 
            >
              <template slot="title">
                <i :class="item.iconCls"></i>
                <span slot="title">{{ item.name }}</span>
              </template>
              <el-menu-item v-for="(child, i) in item.children" :index="child.path" :key="child.path">
                {{ child.name }}
              </el-menu-item>
            </el-submenu>
          </template>
        </el-menu>
      </aside>
      <section class="content-container">
        <div class="grid-content bg-purple-light">
          <el-col :span="24" class="breadcrumb-container">
            <!-- <strong class="title">{{ $route.name }}</strong> -->
            <el-breadcrumb separator="/" class="breadcrumb-inner">
              <el-breadcrumb-item v-for="item in $route.matched" :key="item.path">
                {{ item.name }}
              </el-breadcrumb-item>
            </el-breadcrumb>
          </el-col>
          <el-col :span="24" class="content-wrapper">
            <transition name="fade" mode="out-in">
              <router-view></router-view>
            </transition>
          </el-col>
        </div>
      </section>
    </el-col>
  </el-row>
</template>

<script>
  export default {
    data() {
      return {
        sysName: '智能设备信息采集平台',
        isCollapse: false,
        sysUserName: '',
        sysUserAvatar: '',
        form: {
          name: '',
          region: '',
          date1: '',
          date2: '',
          delivery: false,
          type: [],
          resource: '',
          desc: ''
        }
      }
    },
    methods: {
      onSubmit() {
        console.log('submit!');
      },
      handleOpen() {
        //console.log('handleopen');
      },
      handleClose() {
        //console.log('handleclose');
      },
      handleSelect: function(a, b) {},
      //退出登录
      logout: function() {
        var _this = this;
        this.$confirm('确认退出吗?', '提示', {
          //type: 'warning'
        }).then(() => {
          sessionStorage.removeItem('user');
          _this.$router.push('/login');
        }).catch(() => {

        });
      },
      //折叠导航栏
      collapse: function() {
        this.isCollapse = !this.isCollapse;
      },
      showMenu(i, status) {
        this.$refs.menuCollapsed.getElementsByClassName('submenu-hook-' + i)[0].style.display = status ? 'block' :
          'none';
      },
      openMessageLayer() {

      }
    },
    mounted() {
      var user = sessionStorage.getItem('user');
      if (user) {
        user = JSON.parse(user);
        this.sysUserName = user.name || '';
        this.sysUserAvatar = user.avatar || '';
      }
    }
  }
</script>

<style scoped lang="scss">
  @import '~scss_vars';

  .container {
    position: absolute;
    top: 0px;
    bottom: 0px;
    width: 100%;

    .header {
      height: 60px;
      line-height: 60px;
      background: $color-primary;
      color: #fff;

      .userinfo {
        text-align: right;
        // padding-right: 35px;
        display: flex;
        justify-content: flex-end;
        cursor: pointer;

        > div + div {
          margin-left: 40px;
        }

        .userinfo-inner {
          cursor: pointer;
          color: #fff;
          display: flex;
          justify-content: center;
          align-items: center;

          > span {
            margin-right: 5px;
          }

          img {
            width: 40px;
            height: 40px;
            border-radius: 20px;
            margin: 10px 0px 10px 10px;
            float: right;
          }
        }
      }

      .logo {
        //width:230px;
        height: 60px;
        font-size: 14px;
        padding-left: 20px;
        padding-right: 20px;
        border-color: rgba(238, 241, 146, 0.3);
        border-right-width: 1px;
        border-right-style: solid;

        img {
          width: 40px;
          float: left;
          margin: 10px 10px 10px 18px;
        }

        .txt {
          color: #fff;
        }
      }

      .logo-width {
        width: 230px;
      }

      .logo-collapse-width {
        width: 64px
      }

      .tools {
        padding: 0px 23px;
        width: 14px;
        height: 60px;
        line-height: 60px;
        cursor: pointer;
      }
    }

    .main {
      display: flex;
      // background: #324057;
      position: absolute;
      top: 60px;
      bottom: 0px;
      overflow: hidden;

      aside {
        flex: 0 0 230px;
        width: 230px;

        // position: absolute;
        // top: 0px;
        // bottom: 0px;
        .el-menu {
          height: 100%;
          width: 100%;
        }

        .collapsed {
          width: 64px;

          .item {
            position: relative;
          }

          .submenu {
            position: absolute;
            top: 0px;
            left: 60px;
            z-index: 99999;
            height: auto;
            display: none;
          }

        }
      }

      .menu-collapsed {
        flex: 0 0 64px;
        width: 64px;
      }

      .menu-expanded {
        flex: 0 0 230px;
        width: 230px;
      }

      .content-container {
        // background: #f1f2f7;
        flex: 1;
        // position: absolute;
        // right: 0px;
        // top: 0px;
        // bottom: 0px;
        // left: 230px;
        overflow-y: scroll;
        padding: 20px;

        .grid-content {
          height: 100%;
        }

        .breadcrumb-container {
          padding-bottom: 20px;
          border-bottom: 1px solid rgba(44, 44, 32, 0.3);

          // .title {
          //   width: 200px;
          //   float: left;
          //   color: #475669;
          // }

          .breadcrumb-inner {
            float: left;
          }
        }

        .content-wrapper {
          background-color: #fff;
          box-sizing: border-box;
          margin-top: 20px;
          height: calc(100% - 57px);
        }
      }
    }

    //按钮控制的侧边栏目
    .width-transition {
      transition: width 0.5s;
      -moz-transition: width 0.5s;
      -webkit-transition: width 0.5s;
      -o-transition: width 0.5s;
    }

    //按钮动画 淡入淡出
    // .expaned-enter-active {
    //   transition: opacity 0.5s linear 0.3s;
    // }
    // .expaned-enter,
    // .expaned-leave-to {
    //   opacity: 0;
    // }
  }
</style>



